
import XHeader from 'vux/src/components/x-header/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import { checkLoginToLogin } from '@/api/login'
import * as base from '@/api/base'
import PullTo from 'vue-pull-to'
export default {
name: 'index',
components: {
XHeader,
ButtonTab,
ButtonTabItem,
Flexbox,
FlexboxItem,
Calendar,
Group,
XTable,
Badge,
Datetime,
PullTo
},
data () {
return {
demo2: 'TODAY',
dataList: [],
iconLink: '',
chartsType: 0,
strChartsType: ['day', 'month', 'total'],
page: 0,
limit: 10,
isNotLoadAll: true,
isAgent: base.getSingleUserInfo('user_identity').current === 'agent',
title: '馆内用户消费榜单',
choiceMonth: ''
}
},
created () {
this.title = this.isAgent ? '馆长的业绩榜单' : '馆内用户消费榜单'
checkLoginToLogin(this)
this.fetchList()
},
methods: {
choceMonth (value) {
this.choiceMonth = value
this.chartsType = 3
this.page = 0
this.limit = 10
if (this.choiceMonth) {
this.fetchList()
}
},
loadmore (loaded) {
this.fetchList(true, loaded)
},
stateChange (state) {
if (state === 'pull' || state === 'trigger') {
this.iconLink = '#icon-arrow-bottom'
} else if (state === 'loading') {
this.iconLink = '#icon-loading'
} else if (state === 'loaded-done') {
this.iconLink = '#icon-finish'
}
},
fetchList (isLoad = false, loaded) {
let strUrl = '/api/v1/agents/getusercharts?limit=' + this.limit + '&page=' + this.page
if (this.choiceMonth) {
strUrl += '&date=' + this.choiceMonth
} else {
strUrl += '&type=' + this.strChartsType[this.chartsType]
}
base.fetch(strUrl, null, response => {
let data = response.data
if (data.length === 0) {
this.isNotLoadAll = false
}
this.page += 10
this.limit += 10
if (isLoad) {
this.dataList = this.dataList.concat(data)
loaded && typeof loaded === 'function' && loaded('done')
} else {
this.dataList = data
this.isNotLoadAll = true
}
}, error => {
console.log(error)
}, 'GET')
},
choice (value) {
this.page = 0
this.limit = 10
this.choiceMonth = ''
this.$nextTick(() => {
this.chartsType = value
this.$nextTick(() => {
this.fetchList()
})
})
}
}
}



